@import "~antd/dist/antd.css";

@font-face {
  font-family: "LogoFont";
  src: local("LogoFont"), url(../fonts/neue-montreal/NeueMontreal-Regular.otf);
}

$color-primary: #d0bbfe;
$color-secondary: #605bff;

.flex {
  display: flex;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.flex-1 {
  flex: 1;
}

.items-center {
  align-items: center;
}

.justify-center {
  justify-content: center;
}

.justify-between {
  justify-content: space-between;
}

.justify-end {
  justify-content: flex-end;
}

.justify-start {
  justify-content: flex-start;
}

.flex-wrap {
  flex-wrap: wrap;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.cursor-pointer {
  cursor: pointer;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.text-white {
  color: #fff;
}

.text-black {
  color: #000;
}

.overflow-scroll {
  overflow: scroll;
}

.capitalize {
  text-transform: capitalize;
}

.min-h-screen {
  height: 100vh;
}

.spacer {
  min-height: 30px;
}

.spacer-sm {
  min-height: 13px;
}

.title-1 {
  font-size: 25px;
}

.ant-layout {
  font-family: "Poppins", sans-serif;
}

.ant-layout-header {
  height: 64px;
  padding: 0 50px;
  color: rgba(0, 0, 0, 0.85);
  line-height: 64px;
  background: transparent !important;
}

.ant-layout-sider {
  background: #ffffff !important;
  box-shadow: 0px 10px 60px rgba(226, 236, 249, 0.5);
}

.trigger {
  padding: 0 24px;
  font-size: 18px;
  line-height: 64px;
  cursor: pointer;
  transition: color 0.3s;
}

.trigger:hover {
  color: #1890ff;
}

.logo {
  // height: 40px;
  // margin: 25px;
  // background: rgba(255, 255, 255, 0.3);
  font-size: 25px;
  font-weight: 550;
}
.logo-nav {
  text-transform: uppercase;
  padding: 25px;
  letter-spacing: 0.15em;
  letter-spacing: 3px;
  padding-bottom: 0;
  color: #3d3e53;
  text-align: center;
  font-family: LogoFont, sans-serif !important;
  font-size: 20px;
  font-weight: 600;
  z-index: 10;
}
.logo-container {
  position: absolute;
  right: 10%;
  bottom: 20px;
  // margin-top: 100px;
  // margin-bottom: 20px;
}

.site-base-layout {
  background: #fafaff;
}

.site-layout {
  background: #fafaff !important;
}

.menu-sidebar {
  padding: 25px !important;
}

.menu-sidebar li {
  border-radius: 7px;
}

.ant-menu-item-selected {
  color: #ffffff !important;
}

li.ant-menu-item.ant-menu-item-selected {
  background: $color-primary !important;
}

.ant-menu-vertical .ant-menu-item::after,
.ant-menu-vertical-left .ant-menu-item::after,
.ant-menu-vertical-right .ant-menu-item::after,
.ant-menu-inline .ant-menu-item::after {
  border-right: 0px solid #1890ff !important;
}

span.anticon.ant-menu-item-icon {
  margin-right: 10px;
}

.ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled):hover {
  border-color: $color-primary !important;
}

.ant-input-affix-wrapper:focus,
.ant-input-affix-wrapper-focused {
  border-color: $color-primary !important;
}

.ant-steps-item-process > .ant-steps-item-container > .ant-steps-item-icon {
  background: #fff !important;
  border-color: $color-secondary !important;
}

.ant-steps-item-active .ant-steps-item-finish .ant-steps-item-icon {
  border-color: $color-secondary !important;
}

.ant-steps-item-active .ant-steps-item-icon .ant-steps-icon {
  color: $color-secondary !important;
}

.ant-steps-item-process
  > .ant-steps-item-container
  > .ant-steps-item-content
  > .ant-steps-item-title {
  color: $color-secondary !important;
}

.ant-steps-item-finish .ant-steps-item-icon {
  background: $color-secondary !important;
  border-color: $color-secondary !important;
}

.ant-steps-item-finish .ant-steps-item-icon .ant-steps-icon {
  color: #fff !important;
}

.ant-steps-item-finish
  > .ant-steps-item-container
  > .ant-steps-item-content
  > .ant-steps-item-title {
  color: $color-secondary !important;
}

.ant-slider-track {
  background: $color-secondary !important;
}

.ant-slider-handle {
  border: solid 2px $color-secondary !important;
}

.select-company,
.search-input {
  width: 250px;
}

.select-company
  .ant-select:not(.ant-select-customize-input)
  .ant-select-selector {
  border: none !important;
  border-radius: 6px !important;
}

.user-avatar {
  align-items: center;
  margin-left: 15px;
}

.user-position {
  margin-left: 5px;
}

.search-input .ant-input-affix-wrapper {
  border: none !important;
  border-radius: 7px !important;
}

.ant-card {
  background: #ffffff;
  /* Drop Shadow */
  // box-shadow: 0px 10px 60px rgba(226, 236, 249, 0.5);
  box-shadow: 0px 10px 60px rgba(226, 236, 249, 1);
  border-radius: 8px !important;
}

.synergies-goal:not(:last-child) {
  margin-right: 25px !important;
}

.synergies-goal-icon-0 {
  background-color: #bdc1fe !important;
  margin-bottom: 20px !important;
  border-radius: 8px !important;
}

.synergies-goal-icon-1 {
  background-color: #d0bbfe !important;
  margin-bottom: 20px !important;
  border-radius: 8px !important;
}

.synergies-goal-icon-2 {
  background-color: #eab8ff !important;
  margin-bottom: 20px !important;
  border-radius: 8px !important;
}

.synergies-goal-title {
  font-size: 16px;
  font-weight: bold;
}

.investor-profile-card {
  margin-top: 10px !important;
}

.investor-profile-container {
  line-height: 2.5;
  margin-bottom: 12px;
}

.investor-profile-content {
  color: #9197b3;
  padding-left: 12px;
  padding-right: 12px;
  background-color: #fafafb;
  border-radius: 8px;
  line-height: 2.5;
  overflow-wrap: break-word;
}

.investor-profile-content-multiline {
  color: #9197b3;
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 12px;
  padding-bottom: 12px;
  background-color: #fafafb;
  border-radius: 8px;
  line-height: normal;
  overflow-wrap: break-word;
}

.investor-profile-item {
  justify-content: space-between !important;
  padding: 20px;
  margin-top: 30px;
  background-color: white;
  border-radius: 8px !important;
}

.ai-recommendation-actions {
  display: flex !important;
  flex-direction: row-reverse !important;
}

.leads-card {
  display: flex;
  flex-wrap: wrap;
}

.leads-card-content {
  align-items: center;
  margin-right: 1.5rem;
}

.leads-card-content .ant-card {
  height: 110px !important;
}

.leads-card-icon {
  padding-top: 15px;
  padding-left: 18px;
  padding-right: 18px;
  padding-bottom: 12px;
  background-color: #dbe7ff;
  border-radius: 50%;
  margin-right: 20px;
}

.leads-card-value {
  font-size: 20px;
  font-weight: bold;
}

.leads-card-icon-add {
  padding-top: 6px;
  padding-left: 18px;
  padding-right: 18px;
  padding-bottom: 6px;
  background-color: #dbe7ff;
  border-radius: 50%;
  margin-right: 10px;
}

.steps-content {
  min-height: 200px;
  margin-top: 16px;
  margin-bottom: 24px;
  border-radius: 2px;
}

.steps-action {
  display: flex;
}

.dropdown .ant-select-selector {
  border-radius: 8px !important;
}

.inputs {
  border-radius: 8px !important;
}

.btn {
  height: 38px !important;
  border-radius: 8px !important;
}

.btn-primary {
  color: #ffff !important;
  background-color: $color-secondary !important;
  border-color: $color-secondary !important;
}

.leads-tbl .ant-table {
  border-radius: 12px !important;
}

.leads-tbl .ant-table-thead > tr > th {
  background-color: #ffff !important;
  border-bottom: none !important;
}

.ant-menu-item .anticon {
  color: #5b93ff !important;
}

.ant-menu-item-selected .anticon {
  color: #fff !important;
}

.program-x-modal .ant-modal-content {
  border-radius: 8px;
}

.program-x-modal .title {
  margin-top: 10px;
}

.program-x-modal .description {
  margin-bottom: 30px;
}

.program-x-modal-success-content {
  flex: 1;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 30px;
  margin-bottom: 30px;
}

.custom-notes {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.custom-notes-content {
  width: 300px;
  margin-right: 10px;
}

.custom-notes-content div {
  min-height: 10px;
  max-height: 150px;
  padding: 15px;
  background-color: #fafafb;
  border-radius: 8px;
  overflow-y: scroll;
}

tr.ant-table-expanded-row > td {
  background-color: #fff !important;
}

.progress-bar {
  width: 250px;
}

.no-thead thead {
  display: none !important;
}

.space-x:not(:last-child) {
  margin-right: 25px !important;
}

.landing-layout {
  background-color: #fff !important;
}

.landing-layout .ant-layout-header {
  height: 100px;
  padding-left: 30px;
  padding-right: 30px;
}

.landing-layout .ant-layout-header .menu ul {
  display: flex;
  align-items: center;
}

.landing-layout .ant-layout-header .menu ul li {
  display: flex;
}

.landing-layout .ant-layout-header .menu ul li {
  padding: 20px;
}

.landing-layout .ant-layout-header .menu ul li a {
  color: #000;
  font-size: 16px;
}

.landing-layout .ant-layout-header .menu ul li .ant-btn {
  color: #000;
}

.landing-layout .ant-layout-header .menu ul li .btn {
  color: #fff;
  background-color: #dd97e9;
  border-radius: 8px;
}

.bg-circle {
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  height: 550px;
  width: 550px;
  border-radius: 50%;
  background-color: #fcf3f7;
}

.linechart-nav {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-left: 60px;
  padding-right: 60px;
}

.linechart-nav .toggle-inactive {
  color: #000 !important;
  background-color: #fff;
  border: 1px solid #f4f7ff !important;
}

.linechart-nav .toggle-active {
  color: #fff;
  background-color: #5b93ff;
}

.linecart g path {
  stroke: none;
}

.landing-third-layout {
  background-color: #fff !important;
}

.landing-third-layout .ant-layout-header {
  height: 100px;
  padding-left: 30px;
  padding-right: 30px;
}

.landing-third-layout .menu ul {
  display: flex;
  align-items: center;
}

.landing-third-layout .menu ul li {
  display: flex;
}

.landing-third-layout .menu ul li {
  padding: 20px;
}

.landing-third-layout .menu ul li a {
  color: #000;
  font-size: 16px;
}

.landing-third-layout .menu ul li .ant-btn {
  color: #000;
}

.landing-third-layout .menu ul li .btn {
  color: #fff;
  background-color: #dd97e9;
  border-radius: 8px;
}

.landing-third-layout .footer ul li a {
  color: #fff !important;
}

.landing-third-layout .landing-third-layout-background {
  position: absolute;
  right: 0;
  // width: 100%;
}

.header-bg-gradient {
  position: absolute;
  top: -120px;
  right: 0;
  height: 451px;
  width: 451px;
  border-radius: 50%;
  opacity: 0.7;
  background-color: #eab8ff;
  filter: blur(150px);
}

.footer-bg-gradient {
  position: absolute;
  bottom: -120px;
  left: -80px;
  height: 451px;
  width: 451px;
  border-radius: 50%;
  opacity: 0.7;
  background-color: #d0bbfe;
  filter: blur(150px);
}

.landing-third-layout-third-section-background {
  position: absolute;
  top: -150px;
  background-image: url("../images/bg-lines-2.png");
  background-repeat: no-repeat;
}

.bg-gradient-right {
  position: absolute;
  top: 20px;
  right: 50%;
  left: 40%;
  height: 600px;
  width: 600px;
  border-radius: 50%;
  opacity: 0.7;
  background-color: #d0bbfe;
  filter: blur(150px);
}

.landing-fifth-section {
  // background: linear-gradient(180deg, #D0BBFE 0%, rgba(208, 187, 254, 0.5) 100%);
  background: linear-gradient(
    180deg,
    #999ffb 0%,
    rgba(153, 159, 251, 0.5) 100%
  );
}

.page-loader {
  // width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.goals-breadcrumb {
  color: #000 !important;
}

.react-tag-input {
  padding: 2px !important;
  min-height: 1.5em !important;
  border: 1px solid #d9d9d9 !important;
  border-radius: 8px !important;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

.react-tag-input__input::-webkit-input-placeholder {
  font-size: 14px;
}

.react-tag-input__tag {
  background: #f5f5f5 !important;
}

.react-tag-input__tag__remove {
  background: #f5f5f5 !important;
}

.react-tag-input__tag__remove::before {
  width: 0.12em !important;
  background: #5d575799 !important;
}

.react-tag-input__tag__remove::after {
  width: 0.12em !important;
  background: #5d575799 !important;
}

.goal-value {
  color: #9197b3;
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 12px;
  padding-bottom: 12px;
  background-color: #fafafb;
  border-radius: 8px;
  line-height: normal;
  overflow-wrap: break-word;
  width: 250px;
}

.dynamic-form .ant-select-multiple .ant-select-selection-item-content {
  margin-right: 10px !important;
}

.dynamic-form .ant-select-multiple .ant-select-selection-item-remove {
  font-weight: bolder !important;
}

.metrics {
  font-size: 30px;
  font-weight: 600;
  margin: 0;
}

.dashboard-header {
  font-weight: 600;
}

.ant-menu-inline,
.ant-menu-vertical,
.ant-menu-vertical-left {
  border-right: 0px solid #f0f0f0 !important;
}

.home-card {
  .ant-card-body {
    padding: 13px !important;
  }
  padding: 0 !important;
}

.landing-menu {
  z-index: 20;
}

.mobile-navlinks-container {
  z-index: 10;
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  // margin: 1.5rem 1rem;
  // padding: 2rem;
  border-width: 1px;
  text-align: center;
  border-radius: 0.5rem;
  --tw-text-opacity: 1;
  color: rgba(26, 32, 44, var(--tw-text-opacity));
  --tw-bg-opacity: 1;
  background-color: rgba(255, 255, 255, var(--tw-bg-opacity));

  //
  width: 100vw;
}

.mobile-footer-container {
  z-index: 10;
  position: fixed;
  bottom: 0px;
  left: 0px;
  right: 0px;
  // margin: 1.5rem 1rem;
  // padding: 2rem;
  border-width: 1px;
  text-align: center;
  border-radius: 0.5rem;
  --tw-text-opacity: 1;
  color: rgba(26, 32, 44, var(--tw-text-opacity));
  --tw-bg-opacity: 1;
  background-color: rgba(255, 255, 255, var(--tw-bg-opacity));

  //
  width: 100vw;
}

@media only screen and (max-width: 500px) {
  .dashboard-text {
    margin: 0 auto;
  }
}
